
export interface IDsuSpecModel {
    id: string;
    deviceUniqueId: string;
    ipV4?: string;
    host: string;
    protocol: number;
    prettyProtocol?: string;
    port: number;
    deviceName: string;

    deviceArch: number;
    prettyDeviceArch?: string;
    capacity: number;
    threshold: number;
    apiKey: number;
    rank?: number;
    status?: number;
    prettyStatus?: string;
    usage?: number;
    discoverEndpoint?: string;

    ipAddresses?: string[];
}

export interface IDsuSpecUpdatedModel {
    ipV4?: string;
    host?: string;
    protocol?: number;
    port?: number;
    deviceName?: string;

    deviceArch?: number;
    capacity?: number;
    threshold?: number;
    apiKey?: number;
    status?: number;
    discoverEndpoint?: string;
}

export function calculateDsuSpecChanges(oldSpec: IDsuSpecUpdatedModel, newSpec: IDsuSpecModel): IDsuSpecUpdatedModel {
    const fields = [
        'ipV4',
        'host',
        'protocol',
        'port',
        'deviceName',

        'deviceArch',
        'capacity',
        'threshold',
        'apiKey',
        'status',
        'discoverEndpoint'
    ];

    const ret: IDsuSpecUpdatedModel = {};
    fields.forEach(f => {
        if (newSpec[f] !== oldSpec[f]) {
            ret[f] = newSpec[f];
        }
    });

    return ret;
}

