import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { tap } from 'rxjs/operators';

import { RuntimeConfiguration } from './runtime-configuration';

@Injectable({
    providedIn: 'root'
})
export class DsmUtilitiesService {

    constructor(private readonly _httpClient: HttpClient,
        private readonly _configuration: RuntimeConfiguration) { }

    async discoverEndpoints(serverUrl: string) {
        return await this._httpClient.get(serverUrl + '/' + this._configuration.discoverDSMEndpoint)
            .pipe(
                tap((a: any) => {
                    a.pingFromDSU && (this._configuration.pingDSMEndpoint4DSU = a.pingFromDSU);
                    a.pingFromDSP && (this._configuration.pingDSMEndpoint4DSP = a.pingFromDSP);
                    a.pingFromDSM && (this._configuration.pingDSMEndpoint4DSM = a.pingFromDSM);
                })
            )
            .toPromise();
    }

    async pingFromDSPAsync(serverUrl: string, apiKey: string) {

        return await this._httpClient.get(serverUrl + '/' + this._configuration.pingDSMEndpoint4DSP, {
            headers: new HttpHeaders().set('X-Api-Key', apiKey)
        }).toPromise();
    }

    async pingFromDSUAsync(serverUrl: string, apiKey: string) {

        return await this._httpClient.get(serverUrl + '/' + this._configuration.pingDSMEndpoint4DSU, {
            headers: new HttpHeaders().set('X-Api-Key', apiKey)
        }).toPromise();
    }

    async pingFromDSMAsync(serverUrl: string, apiKey: string) {

        return await this._httpClient.get(serverUrl + '/' + this._configuration.pingDSMEndpoint4DSM, {
            headers: new HttpHeaders().set('X-Api-Key', apiKey)
        }).toPromise();
    }

}
