import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { tap } from 'rxjs/operators';

import { IDsmSettingsModel } from '../models/dsm-settings.model';
import { RuntimeConfiguration } from './runtime-configuration';

/* Read and write dsm settings */

@Injectable({
    providedIn: 'root',
})
export class DsmSettingsRWService {

    private _cached: IDsmSettingsModel;

    constructor(private readonly _httpClient: HttpClient,
        private readonly _runtimeConfiguration: RuntimeConfiguration) {
    }

    async readAsync(checkCache: boolean = true) {
        if (checkCache && this._cached) {
            return this._cached;
        }

        return await this._httpClient.get(this._runtimeConfiguration.dsmSettingsPath)
            .pipe(
                tap((a: IDsmSettingsModel) => {
                    if (a && a.serverApiKey && a.serverUrl) {
                        this._cached = a;
                        this._runtimeConfiguration.dsmServerUrl = a.serverUrl;
                        this._runtimeConfiguration.dsmServerApiKey4DSU = a.serverApiKey;

                    }
                })
            ).toPromise();
    }

    async writeAsync(data: IDsmSettingsModel) {
        return await this._httpClient.post(this._runtimeConfiguration.dsmSettingsPath, data)
            .pipe(
                tap(_ => {
                    this._cached = data;
                    this._runtimeConfiguration.dsmServerUrl = data.serverUrl;
                    this._runtimeConfiguration.dsmServerApiKey4DSU = data.serverApiKey;
                })
            ).toPromise();
    }
}
