import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators, FormGroup, FormControl } from '@angular/forms';

import {
    MessageSeverity,
    AlertServiceAbstractProvider,
    IAlertServiceContract,
    ITranslationServiceContract,
    TranslationServiceAbstractProvider
} from '@polpware/ngx-appkit-contracts-alpha';

import {
    HostReservedDiskRWService,
    IHostReservedDiskModel
} from '../../services/host-reserved-disk-rw.service';

@Component({
    selector: 'host-reserved-disk-form',
    templateUrl: './host-reserved-disk-form.component.html',
    styleUrls: ['./host-reserved-disk-form.component.scss']
})
export class HostReservedDiskFormComponent implements OnInit {

    readonly form: FormGroup;
    alertMessage: string;
    alertType: 'alert-info' | 'alert-success' | 'alert-danger' | '';

    private readonly _alertService: IAlertServiceContract;
    readonly translator: ITranslationServiceContract;

    public deviceInfo: IHostReservedDiskModel;

    constructor(private readonly _diskService: HostReservedDiskRWService,
        alertServiceProvider: AlertServiceAbstractProvider,
        translateServiceProvicer: TranslationServiceAbstractProvider,
        formBuilder: FormBuilder) {

        this._alertService = alertServiceProvider.get();
        this.translator = translateServiceProvicer.get();

        this.form = formBuilder.group({
            storageRootFullPath: new FormControl('', { validators: [Validators.required] })
        });
    }

    get storageRootFullPath() { return this.form.get('storageRootFullPath'); }

    ngOnInit() {

        this._diskService.readAsync()
            .then(a => {
                this.deviceInfo = a;
                this.storageRootFullPath.setValue(a.storageRootFullPath);
                this.enable(false, null);
            });
    }

    enable(flag: boolean, evt: Event) {
        evt && evt.preventDefault();
        evt && evt.stopPropagation();

        if (flag) {
            this.form.enable();
        } else {
            this.form.disable();
            this.alertMessage = '';
            this.alertType = '';
        }
    }

    submit() {
        // Assuming that the values to be submitted are new and valid.
        // todo: Progress
        this.alertType = 'alert-info';
        this.alertMessage = "Submitting ...";
        this._diskService.writeAsync(this.form.value)
            .then(_ => {
                // notify message                
                this.alertType = 'alert-success';
                this.alertMessage = "Just updated successfully.";
                this.form.disable();
                this._alertService.showMessage('Storage Space Root', 'You just updated the full path of the storage space root!', MessageSeverity.success);
            }, _ => {
                this.alertType = 'alert-danger';
                this.alertMessage = 'Something went wrong!';
            });

    }

}
