import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { tap } from 'rxjs/operators';

import { RuntimeConfiguration } from './runtime-configuration';

export interface IHostReservedDiskModel {
    storageRootFullPath: string;
    tempDirName?: string;
    stagedDirName?: string;
    commitedDirName?: string;
    availableDrivers?: [{
        rootDirectory: string;
        totalSpace: number;
        freeSpace: number;
    }]
}

@Injectable({
    providedIn: 'root'
})
export class HostReservedDiskRWService {

    private _cached: IHostReservedDiskModel;

    constructor(private readonly _httpClient: HttpClient,
        private readonly _runtimeConfiguration: RuntimeConfiguration) { }

    async readAsync(checkCache: boolean = true) {

        if (checkCache && this._cached) {
            return this._cached;
        }

        return await this._httpClient.get(this._runtimeConfiguration.hostReservedDiskSettingPath)
            .pipe(
                tap((a: IHostReservedDiskModel) => {
                    this._cached = a;
                })
            ).toPromise();
    }

    async writeAsync(data: IHostReservedDiskModel) {
        return await this._httpClient.post(this._runtimeConfiguration.hostReservedDiskSettingPath, data)
            .pipe(
                tap(_ => {
                    this._cached.storageRootFullPath = data.storageRootFullPath;
                })
            ).toPromise();
    }

}
