import { Component, OnInit, Input } from '@angular/core';
import { FormBuilder, Validators, FormGroup, FormControl } from '@angular/forms';

import { DsmSettingsRWService } from '../../services/dsm-settings-rw.service';
import { DsmUtilitiesService } from '../../services/dsm-utilities.service';

import {
    MessageSeverity,
    AlertServiceAbstractProvider,
    IAlertServiceContract,
    ITranslationServiceContract,
    TranslationServiceAbstractProvider
} from '@polpware/ngx-appkit-contracts-alpha';

interface IStepProgress {
    action: string;
    status: string;
}

@Component({
    selector: 'css-dsm-settings-form',
    templateUrl: './dsm-settings-form.component.html',
    styleUrls: ['./dsm-settings-form.component.scss']
})
export class DsmSettingsFormComponent implements OnInit {

    @Input() deviceUnit: 'dsu' | 'dsm' | 'dsp' = 'dsu';
    readonly form: FormGroup;
    alertMessage: string;
    alertType: 'alert-info' | 'alert-success' | 'alert-danger' | '';

    passCheck: boolean;
    steps: Array<IStepProgress> = [];

    private readonly _alertService: IAlertServiceContract;
    readonly translator: ITranslationServiceContract;

    constructor(private readonly _rwService: DsmSettingsRWService,
        private readonly _utilitiesService: DsmUtilitiesService,
        alertServiceProvider: AlertServiceAbstractProvider,
        translateServiceProvicer: TranslationServiceAbstractProvider,
        formBuilder: FormBuilder) {

        this._alertService = alertServiceProvider.get();
        this.translator = translateServiceProvicer.get();

        this.form = formBuilder.group({
            serverUrl: new FormControl('', { validators: [Validators.required] }),
            serverApiKey: new FormControl('', { validators: [Validators.required] })
        });
    }

    get serverUrl() { return this.form.get('serverUrl'); }
    get serverApiKey() { return this.form.get('serverApiKey'); }

    ngOnInit() {
        this._rwService.readAsync()
            .then(a => {
                if (a.serverUrl && a.serverApiKey) {
                    this.form.setValue(a);
                    // Disable it
                    this.form.disable();
                }
            });
    }

    enable(flag: boolean, evt: Event) {
        evt.preventDefault();
        evt.stopPropagation();

        if (flag) {
            this.form.enable();
        } else {
            this.form.disable();
            this.alertMessage = '';
            this.alertType = '';
            this.steps = [];
        }
    }

    async preSubmit() {
        // Assuming that the values to be submitted are new and valid.
        // todo: Progress
        this.passCheck = false;
        this.steps = [];
        this.alertType = 'alert-info';
        this.alertMessage = "Verifying DSM Information";

        const newServerUrl = this.serverUrl.value;
        const newServerApiKey = this.serverApiKey.value;

        try {
            this.steps.push({ action: 'Connecting to DSM ...', status: '' });
            await this._utilitiesService.discoverEndpoints(newServerUrl);
            this.steps[this.steps.length - 1].status = 'Ok';

            this.steps.push({ action: 'Validating API Key ...', status: '' });
            if (this.deviceUnit == 'dsu') {
                await this._utilitiesService.pingFromDSUAsync(newServerUrl, newServerApiKey);
            } else if (this.deviceUnit == 'dsp') {
                await this._utilitiesService.pingFromDSPAsync(newServerUrl, newServerApiKey);
            } else {
                await this._utilitiesService.pingFromDSMAsync(newServerUrl, newServerApiKey);
            }

            this.steps[this.steps.length - 1].status = 'Ok';

            this.passCheck = true;
            this.alertType = 'alert-success';
        }
        catch {
            this.steps[this.steps.length - 1].status = 'Error';
            this.alertType = 'alert-danger';
            this.alertMessage = 'Fail to verify DSM information!';
        }
    }

    submit() {
        // Assuming that the values to be submitted are new and valid.
        // todo: Progress
        this.alertType = 'alert-info';
        this.alertMessage = "Submitting ...";
        this._rwService.writeAsync(this.form.value)
            .then(_ => {
                // notify message                
                this.alertType = 'alert-success';
                this.alertMessage = "Just updated successfully.";
                this.form.disable();
                this._alertService.showMessage('DSM Settings Updated', 'You just updated DSM Settings!', MessageSeverity.success);
            }, _ => {
                this.alertType = 'alert-danger';
                this.alertMessage = 'Something went wrong!';
            });

    }
}
