import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { RuntimeConfiguration } from './runtime-configuration';
import { IDsuSpecModel, IDsuSpecUpdatedModel } from '../models/dsu-spec.model';

@Injectable({
    providedIn: 'root'
})
export class DsuRegistarService {

    constructor(private readonly _httpClient: HttpClient,
        private readonly _runtimeConfiguration: RuntimeConfiguration) {
    }

    async getByUniqueIdAsync(deviceUniqueId: string): Promise<IDsuSpecModel> {
        const url = this._runtimeConfiguration.dsmServerUrl +
            this._runtimeConfiguration.dsuLookupPath + '/' +
            deviceUniqueId;
        return await this._httpClient.get<IDsuSpecModel>(url)
            .toPromise();
    }

    async registerAsync(data: IDsuSpecModel) {
        const url = this._runtimeConfiguration.dsmServerUrl +
            this._runtimeConfiguration.dsuRegistarPath;
        return await this._httpClient.post<string>(url, data, {
            headers: new HttpHeaders({
                'Content-Type': 'application/json'
            })
        }).toPromise();
    }

    async updateByUniqueIdAsync(deviceUniqueId: string, data: IDsuSpecUpdatedModel) {

        const patchData = Object.keys(data).map(k => {
            return {
                'op': 'add',
                'path': '/' + k,
                'value': data[k]
            };
        });

        const url = this._runtimeConfiguration.dsmServerUrl +
            this._runtimeConfiguration.dsuUpdatePath + '/' +
            deviceUniqueId;
        return await this._httpClient.patch(url, patchData, {
            headers: new HttpHeaders({
                'Content-Type': 'application/json'
            })
        }).toPromise();
    }

    async undoRegisterByEntityIdAsync(entityId: string) {
        const url = this._runtimeConfiguration.dsmServerUrl +
            this._runtimeConfiguration.dsuUndoRegisterPath + '/' + entityId;
        return await this._httpClient.delete(url)
            .toPromise();
    }


}
